<template>
  <div class="paper-answer-scrollBox" style="height:100%;">
    <div class="paddding16" v-scroll="{autoHideScrollbar:true}">
      <!-- <div class="paddding16"> -->
      <div>
        <div class="pac-notic" v-if="isPC">
          <div class="pacn-title"></div>
          <div class="pacn-text">30 </div>
        </div>
        <div class="question-area" v-if="questions.length">
          <div class="qa-title" :class="{'h5-qa-title':!isPC}">
            {{questions.length}}
          </div>
          <div class="qa-content">

            <!--  -->
            <div v-if="currentIndex <= questions.length">
              <!-- [MBTI] -->
              <div v-for="question in questions" :key="question.vForKey" v-if="type==='mbti' && currentIndex === question.orderIndex">
                <div class="qac-bigInfo" :class="{'h5-qac-bigInfo':!isPC}" v-if="question.cate">{{question.cate}}</div>
                <div class="qac-options">
                  <div class="qus-title">
                    <div :class="{'ques-num':isPC}">
                      <span class="ques-num-current">{{question.orderIndex}}</span>
                      <span class="ques-num-sum">/{{questions.length}}</span>
                    </div>
                    <div class="h5-pcSpace" v-if="!isPC"></div>
                    <div class="ques-body" :class="{'h5-ques-body':!isPC}">
                      <div class="ques-text" :class="{'h5-ques-text':!isPC}" v-html="question.question"></div>
                      <div class="op-item" :class="{'h5-op-item':!isPC}" @click="goToNextQuestion(question,'mbti', 'A')">
                        <div :class='{"h5-op-select":!isPC}'>
                          A
                        </div>
                        <div>{{question.option_A | optionFilter}}</div>
                      </div>
                      <div class="op-item" :class="{'h5-op-item':!isPC}" @click="goToNextQuestion(question,'mbti' ,'B')">
                        <div :class='{"h5-op-select":!isPC}'>
                          B
                        </div>
                        <div>{{question.option_B | optionFilter}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <!-- [holland] -->
              <div v-for="question in questions" :key="question.vForKey" v-if="type==='holland' && currentIndex === question.orderIndex">
                <div class="qac-bigInfo" v-if="question.cate">{{question.cate}}</div>
                <div class="qac-options">
                  <div class="qus-title">
                    <div :class="{'ques-num':isPC}">
                      <span class="ques-num-current">{{question.orderIndex}}</span>
                      <span class="ques-num-sum">/{{questions.length}}</span>
                    </div>
                    <!-- <div class="h5-pcSpace" v-if="!isPC"></div> -->
                    <div class="ques-body" :class="{'h5-ques-body':!isPC}">
                      <div class="ques-text" :class="{'h5-ques-text':!isPC}" v-html="question.question"></div>
                      <div class="op-item" :class="{'h5-op-item':!isPC}" @click="goToNextQuestion(question,'holland', 'A')">
                        <div>
                          A
                        </div>
                        <div>{{question.option_Y}}</div>
                      </div>
                      <div class="op-item" :class="{'h5-op-item':!isPC}" @click="goToNextQuestion(question,'holland' ,'B')">
                        <div>
                          B
                        </div>
                        <div>{{question.option_N}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <!-- [profession] -->
              <div v-for="question in questions" :key="question.vForKey" v-if="type==='profession' && currentIndex === question.orderIndex">
                <div class="qac-bigInfo" v-if="question.cate">{{question.cate}}</div>
                <div class="qac-options">
                  <div class="qus-title">
                    <div :class="{'ques-num':isPC}">
                      <span class="ques-num-current">{{question.orderIndex}}</span>
                      <span class="ques-num-sum">/{{questions.length}}</span>
                    </div>
                    <!-- <div class="h5-pcSpace" v-if="!isPC"></div> -->
                    <div class="ques-body" :class="{'h5-ques-body':!isPC}">
                      <div class="ques-text" :class="{'h5-ques-text':!isPC}" v-html="question.question"></div>
                      <div class="op-item" :class="{'h5-op-item':!isPC}" @click="goToNextQuestion(question,'profession', 'A')">
                        <div>
                          A
                        </div>
                        <div>{{question.option_A}}</div>
                      </div>
                      <div class="op-item" :class="{'h5-op-item':!isPC}" @click="goToNextQuestion(question,'profession' ,'B')">
                        <div>
                          B
                        </div>
                        <div>{{question.option_B}}</div>
                      </div>
                      <div class="op-item" :class="{'h5-op-item':!isPC}" @click="goToNextQuestion(question,'profession' ,'C')">
                        <div>
                          C
                        </div>
                        <div>{{question.option_C}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <!--  -->
            <div v-if=" currentIndex > questions.length ">
              <div class="qac-bigInfo">{{''}}</div>
              <div class="qac-options">
                <div class="qus-title">
                  <div class="last-question">
                    
                  </div>
                </div>
              </div>
            </div>

          </div>
          <div class="ques-submit" v-if="currentIndex > questions.length">
            <div class="qs-sbumitBtn" @click="submitPaperAnser"></div>
          </div>
        </div>

        <!--  -->
        <div class="signUp-mask" v-if="signUpIf">
          <div class="signUp-form">
            <el-form :model="signUpForm" ref="signUpForm" label-width="80px">
              <el-form-item label="" prop="name" :rules="signUpFormRule.name">
                <el-input v-model.number="signUpForm.name" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="phone" :rules="signUpFormRule.phone">
                <el-input v-model.number="signUpForm.phone" placeholder=""></el-input>
              </el-form-item>
              <el-form-item class="yzInput" label="" prop="securityCode" :rules="signUpFormRule.securityCode">
                <el-input v-model.number="signUpForm.securityCode" placeholder=""></el-input>
                <el-button type="primary" plain @click="getSecurityCode" :loading="securityBtn.loading">{{securityBtn.text}}</el-button>
              </el-form-item>
              <el-button class="signUp-submitBtn" type="primary" size="medium" @click="phoneSubmit">&nbsp;&nbsp;</el-button>
            </el-form>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
import { Loading } from "element-ui";
import {
  saveHolland,
  getHolland,
  getMbti,
  saveMbti,
  getMajor,
  saveMajor,
  getPhoneCode
} from "@/api/http.js";
import config from "@/config.js";
import common from "@/common.js";
export default {
  data() {
    // //todo: 
    // if (!Object.keys(this.$route.params).length) {
    //   document.cookie = "zbxk-token= ";
    //   document.cookie = "zbxk-userInfo= ";
    //   localStorage.setItem("zbxk-current-exam ", "");
    //   localStorage.setItem("zbxk-cp-type", "");
    //   this.$router.push({ name: "login" });
    //   return {
    //     signUpIf: false, //
    //     questions: []
    //   };
    // }

    /**
     * @param:type
     *    mbti: 
     *    holland:
     *    profession: 
     */
    var type = localStorage.getItem("zbxk-cp-type");
    var currentExam = JSON.parse(localStorage.getItem("zbxk-current-exam"));
    var codeTale = {
      mbti: {
        get: getMbti,
        save: saveMbti
      },
      holland: {
        get: getHolland,
        save: saveHolland
      },
      profession: {
        get: getMajor,
        save: saveMajor
      }
    };

    //u
    var fullPath = this.$route.fullPath,
      qpString = "",
      queryArray = [],
      qpObject = {};
    // fullPath.replace(/(?<=(\/main\/psychologicalpaper\s*\?)).*/g, function(
    //   match
    // ) {
    //   qpString = match;
    //   return match;
    // });
    fullPath.replace(/\/main\/psychologicalpaper\s*\?(.*)/g, function(
      match,$1
    ) {
      qpString = $1;
      return match;
    });
    queryArray = qpString ? qpString.split("&") : [];
    if (queryArray.length) {
      queryArray.forEach(function(item) {
        var params = item.split("=");
        ["test_id", "school_id", "sno", "sname"].forEach(function(key) {
          if (item.indexOf(key) !== -1) {
            qpObject[key] = params[1];
          }
        });
      });
    }

    //
    function isvalidPhone(str) {
      const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
      return reg.test(str);
    }
    var validPhone = (rule, value, callback) => {
      if (!isvalidPhone(value)) {
        callback(new Error("11"));
      } else {
        callback();
      }
    };

    return {
      config,
      apiSet: codeTale[type],
      type,
      userInfo: JSON.parse(localStorage.getItem("zbxk-userInfo")),
      common,
      currentExam,
      getPhoneCodeAPI: getPhoneCode,
      Loading,

      loadingInstance: null,

      //pc
      isPC: false,

      //
      routerInfo: qpObject,

      //
      signUpForm: {
        name: "",
        phone: "",
        securityCode: ""
      },
      signUpFormRule: {
        name: [{ required: true, message: "" }],
        phone: [
          { required: true, message: "" },
          { type: "number", message: "" },
          { trigger: "change", validator: validPhone }
        ],
        securityCode: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ]
      },
      securityBtn: {
        loading: false,
        text: ""
      },
      signUpIf: false, //
      questions: [],
      currentIndex: 1, //
      record: {} //

      // startTest:false, //
    };
  },
  mounted() {
    var that = this;
    this.$nextTick(function() {
      that.getQuestions();

      // // 
      // switch (that.type) {
      //   case "mbti":
      //     that.setMbtiData();
      //     break;
      //   case "holland":
      //     that.setHollandData();
      //     break;
      //   case "profession":
      //     that.setProfessionData();
      //     break;
      // }

      //nextTick layout
      var newTitle = "";
      switch (that.type) {
        case "mbti":
          newTitle = "";
          break;
        case "holland":
          newTitle = "";
          break;
        case "profession":
          newTitle = "";
          break;
      }
      that.bus.$emit("toChangeTitle", newTitle);

      if (that.common.checkPc()) {
        that.isPC = true;
      }
    });
  },
  methods: {
    //loadings
    loadingOpen: function(text) {
      this.loadingInstance = this.Loading.service({
        fullscreen: true,
        lock: true,
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.8)",
        text: text ? text : "..."
      });
    },
    loadingClose: function() {
      if (
        this.loadingInstance &&
        typeof this.loadingInstance.close === "function"
      ) {
        this.loadingInstance.close();
      }
    },
    //
    backPsychological() {
      //
      var params = { name: "psychological" };
      var router_keys = Object.keys(this.routerInfo);
      var routesParamsObj = this.routerInfo;
      if (router_keys.length) {
        params.query = {};
        router_keys.forEach(function(key) {
          params.query[key] = routesParamsObj[key];
        });
      }
      //
      this.$router.push(params);
    },

    // 
    getQuestions() {
      this.loadingOpen("...");
      this.apiSet.get(this.getQueryParam()).then(this.success, this.error);
    },
    //
    getQueryParam() {
      var param = null;
      switch (this.type) {
        case "mbti":
        case "holland":
        case "profession":
          param = {
            // test_id: this.currentExam.test_id,
            // school_id: this.userInfo.school_id

            //
            test_id: 529,
            school_id: 100544
          };
          break;
        default:
          break;
      }
      return param;
    },
    //
    success: function(res) {
      var result = res.data;
      if (result.code == 200) {
        this.querySuccessBack(result.data);
      } else {
        this.$message.error(result.message);
      }
      this.loadingClose();
    },
    //
    error: function(res) {
      this.$message.error("");
    },
    //v-for
    querySuccessBack(source) {
      switch (this.type) {
        case "mbti":
          this.setMbtiData(source);
          break;
        case "holland":
          this.setHollandData(source);
          break;
        case "profession":
          this.setProfessionData(source);
          break;
        default:
          break;
      }
    },
    //mbti
    setMbtiData(source) {
      // //
      // var source = {
      //   code: 200,
      //   message: "",
      //   data: window.mbti
      // };
      // source = source.data;

      var _data = [];
      for (var index in source) {
        var item = source[index];
        for (var subIndex in item) {
          if (subIndex === "cate") {
            continue;
          }
          var subQuestion = item[subIndex].question,
            _cate = item.cate || "";
          subQuestion.cate = _cate;
          subQuestion.orderIndex = Number(subIndex);
          subQuestion.question = subQuestion.question
            ? subQuestion.question
            : '<span style="color:#fff;opacity: 0;"></span>';
          _data.push(subQuestion);
        }
      }
      this.editDataQuestions(_data);
    },
    //Holland
    setHollandData(source) {
      // //
      // var source = {
      //   code: 200,
      //   message: "",
      //   data: window.holland
      // };
      // source = source.data;

      var _data = [];
      for (var key in source) {
        var subQuestion = source[key].question;
        subQuestion.orderIndex = Number(key);
        subQuestion.question = subQuestion.question
          ? subQuestion.question
          : '<span style="color:#fff;opacity: 0;"></span>';
        _data.push(subQuestion);
      }
      this.editDataQuestions(_data);
    },
    //Profession
    setProfessionData(source) {
      // //
      // var source = {
      //   code: 200,
      //   message: "",
      //   data: window.profession
      // };
      // source = source.data;

      var _data = [];
      for (var key in source) {
        var subQuestion = source[key].question;
        subQuestion.orderIndex = Number(key);
        subQuestion.question = subQuestion.question
          ? subQuestion.question
          : '<span style="color:#fff;opacity: 0;"></span>';
        _data.push(subQuestion);
      }
      this.editDataQuestions(_data);
    },
    editDataQuestions: function(source) {
      var _data = source;
      _data.sort(function(pre, next) {
        return pre.orderIndex - next.orderIndex;
      });
      this.questions = this.questions.concat(this.common.tagSource(_data));
      // console.log(_data);
    },

    //:
    goToNextQuestion(data, type, answer) {
      this.currentIndex = data.orderIndex + 1;
      if (this.currentIndex > this.questions.length) {
        //
        this.$message.info("!");
      }
      switch (type) {
        case "mbti":
          this.mbtiGoNext(data, answer);
          break;
        case "holland":
          this.hollandGoNext(data, answer);
          break;
        case "profession":
          this.professionGoNext(data, answer);
          break;
        default:
          break;
      }
      // console.log(this.record);
    },
    mbtiGoNext(data, answer) {
      this.commonRecord(data, answer);
    },
    hollandGoNext(data, answer) {
      this.commonRecord(data, answer);
    },
    professionGoNext(data, answer) {
      this.commonRecord(data, answer);
    },
    //
    commonRecord: function(data, anser) {
      this.record[data.orderIndex] = anser;
    },

    //
    submitPaperAnser() {
      // tokoen
      /**
       * 1.   token
       * 2.  2"test_id", "school_id" token
       * 3.  2"test_id", "school_id" token
       * 4.   4"test_id", "school_id", "sno", "sname" token
       */
      var hastoken = this.config.getCookie("zbxk-token") ? true : false,
        routerParams = this.routerInfo,
        routerParamsLength = Object.keys(routerParams).length,
        ifs = hastoken + "&" + routerParamsLength,
        generalParams = JSON.parse(
          localStorage.getItem("zbxk-paperSaveParams")
        );
      switch (ifs) {
        case "true&0":
          this.savePaperAction(generalParams, this.submitSuccess);
          break;
        case "true&2":
          this.savePaperAction(generalParams, this.submitSuccess);
          break;
        case "false&2":
          //todo
          this.signUpIf = true;
          break;
        case "false&4":
          this.savePaperAction(generalParams, this.submitSuccess);
          break;
      }
    },
    //
    savePaperAction(params, successBack) {
      var params = params;
      params.record = JSON.stringify(this.record);
      this.loadingOpen("...");
      this.apiSet.save(params).then(successBack, this.error);
    },
    submitSuccess(res) {
      var result = res.data;
      if (result.code == 200) {
        this.loadingClose();
        this.$message.success("");
        this.backPsychological(); //
      } else {
        this.loadingClose();
        this.$message.error(result.message);
      }
    },
    error() {
      this.loadingClose();
      this.$message.error("");
    },

    /**
     * -
     */
    // 
    getSecurityCode() {
      var that = this;
      this.$refs.signUpForm.validateField("phone", function(validInfo, valid) {
        var isPhoneValid = !validInfo && !valid ? true : false;
        if (!isPhoneValid) {
          that.$message.warning("");
          return;
        } else {
          var seconds = 30;
          that.securityBtn.loading = true;
          that.securityBtn.text = "" + seconds + "";
          var _timeout = setInterval(function() {
            seconds--;
            if (seconds === 0) {
              clearInterval(_timeout);
              that.securityBtn.loading = false;
              that.securityBtn.text = "";
            } else {
              that.securityBtn.text = "" + seconds + "";
            }
          }, 1000);
          //
          that
            .getPhoneCodeAPI({
              phone_num: that.signUpForm.phone
            })
            .then(function(res) {
              if (res.data.code === 200) {
                that.$message.success("");
              }
            }, that.error);
        }
      });
    },
    //
    phoneSubmit() {
      var that = this;
      this.$refs["signUpForm"].validate(function(valid) {
        if (valid) {
          var generalParams = JSON.parse(
            localStorage.getItem("zbxk-paperSaveParams")
          );

          //
          generalParams.sno = that.signUpForm.phone;
          generalParams.phone = that.signUpForm.phone;
          generalParams.sms_code = that.signUpForm.securityCode;
          generalParams.username = that.signUpForm.name;
          that.savePaperAction(generalParams, that.phoneSuccessBack);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    phoneSuccessBack(res) {
      var result = res.data;
      if (result.code === 200) {
        //
        this.autoLogin();
      } else {
        this.loadingClose();
        this.$message.error(result.message);
      }
    },
    //
    autoLogin() {
      this.qrloginAPI();
    },
    //
    getUserInfo() {
      var that = this,
        config = this.config;
      var url = config.apiDomain + "/user/info";
      this.axios.get(url, { headers: config.header() }).then(
        res => {
          // 
          var data = res.data;
          if (data.code == 200) {
            localStorage.setItem("zbxk-userInfo", JSON.stringify(data.data)); //token
          }
          this.loadingClose();
        },
        res => {
          // 
          console.log(res);
          this.loadingClose();
        }
      );
    },
    //
    qrloginAPI() {
      var that = this,
        config = this.config;
      // 
      var url = config.apiDomain + "/student/account-login";
      //todo: 5IP2
      var codeUrl = config.apiDomain + "/user/captcha";
      var loginData = {
        account: this.signUpForm.phone,
        password: "666666" //todo:
      };
      this.axios.post(url, loginData, { headers: config.headers }).then(
        res => {
          // 
          var data = res.data;
          if (data.code == 200) {
            config.setCookie("zbxk-token", data.data.token, 1); //token
            that.getUserInfo();
          } else {
            that.$message({
              message: data.message,
              type: "warning"
            });
            this.loadingClose();
          }
        },
        res => {
          // 
          console.log(res);
          this.loadingClose();
        }
      );
    }
  },
  filters: {
    optionFilter(value) {
      function filterAB() {
        return new RegExp("^(\\s*[AB]\\.)", "");
      }
      return (value || "").replace(filterAB(), "");
    }
  }
};
</script>
<style>
/*  */
.paddding16 {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 16px;
}

.paper-container {
  /* height: 100%; */
  padding: 20px;
  box-shadow: 0 0 8px 0 rgba(40, 64, 80, 0.08);
  border-radius: 4px;
  margin: 16px;
  background: #fff;
}
.pac-notic {
  background: #fffcf7;
  border: 1px solid #fbaa00;
  border-radius: 4px;
  font-size: 14px;
  color: #fbaa00;
  letter-spacing: 0;
  line-height: 18px;
  text-align: left;
  padding: 20px;
  margin-bottom: 25px;
}
.pacn-title {
  font-size: 20px;
  color: #fbaa00;
  letter-spacing: 0;
  line-height: 20px;
  margin-bottom: 10px;
}
.pacn-text {
  font-size: 14px;
  color: #fbaa00;
  letter-spacing: 0;
  line-height: 18px;
}

/*  */
.question-area {
  text-align: left;
}
.qa-title {
  font-size: 20px;
  color: #284050;
  letter-spacing: 0;
  line-height: 20px;
  margin-bottom: 15px;
}
.qa-content {
  background: #ffffff;
  border: 1px solid #ebefef;
  box-shadow: 0 0 8px 0 rgba(215, 220, 223, 0.5);
  border-radius: 4px;
}
.qac-bigInfo {
  font-size: 16px;
  color: #6d8392;
  letter-spacing: 0;
  line-height: 16px;
  padding: 20px 24px;
  border-bottom: 1px solid #ebefef;
}

.qus-title {
  /* padding: 0 68px; */
  min-height: 58px;
  line-height: 28px;
  margin-top: 20px;
  padding-bottom: 88px;
}
.qus-title:after {
  content: "";
  display: block;
  width: 0;
  clear: both;
}
.qus-title > div {
  display: inline-block;
  float: left;
  /* margin-left: 20px; */
}
.ques-num {
  /* width: 44px;
  height: 48px; */
  /* position: absolute;
  margin-left: -45px; */
  padding: 20px;
}
.ques-body {
  margin-left: 10px;
}
.ques-text {
  font-size: 20px;
  color: #284050;
  letter-spacing: 0;
  line-height: 28px;
  margin-top: 20px;
  margin-bottom: 48px;
}
.ques-num-current {
  font-size: 48px;
  color: #4393f7;
  /* line-height: 48px; */
  float: left;
}
.ques-num-sum {
  font-size: 14px;
  color: #6d8392;
  /* line-height: 68px; */
  float: left;
  position: relative;
  bottom: -10px;
}

/* option */
.op-item {
  background: #ffffff;
  border: 1px solid #d7dce0;
  border-radius: 4px;
  padding: 15px 24px;
  margin-bottom: 16px;
  width: 400px;
  box-sizing: border-box;

  /*  */
  font-size: 16px;
  color: #284050;
  letter-spacing: 0;
  line-height: 16px;
  padding-left: 50px;
  position: relative;
}
/* .op-item:after{
    content:'';
    display: block;
    width:0;
    clear: both;
} */
.op-item:hover {
  cursor: pointer;
  background: #4393f7;
  color: #fff;
}
.op-item > div {
  display: inline-block;
  margin-right: 10px;
  /* float: left; */
}
.op-item > div:first-child {
  position: absolute;
  left: 24px;
  top: 18px;
}
.op-item > div:last-child {
  line-height: 22px;
}

.ques-submit {
  margin-top: 50px;
  text-align: center;
}
.qs-sbumitBtn {
  display: inline-block;
  margin: 0 auto;
  background: #4393f7;
  border-radius: 4px;
  padding: 9px 65px;
  color: #fff;
}
.qs-sbumitBtn:hover {
  opacity: 0.8;
  cursor: pointer;
}

/*  */
.last-question {
  font-size: 16px;
  color: #6d8392;
  letter-spacing: 0;
  line-height: 16px;
  padding: 20px 24px;
  text-align: center;
  margin-bottom: 20px;
}

/*  */
.signUp-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #28405040;
  z-index: 100;
  top: 0;
  left: 0;
}
.signUp-form {
  background: #fff;
  border-radius: 5px;
  border: 1px solid #ebefef;
  width: 100%;
  height: 280px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 25px 10px;
  position: absolute;
}
.signUp-form .el-form {
  position: relative;
  left: -10px;
}
.yzInput .el-form-item__content .el-input {
  display: inline-block;
  width: 53%;
  float: left;
}

.signUp-form .el-button--primary.is-plain {
  width: 45%;
  padding: 12px;
}
.signUp-submitBtn {
  padding-left: 40px;
  padding-right: 40px;
}

/* h5 */
.h5-ques-text {
  font-size: 1rem;
  margin-bottom: 2rem;
  margin-top: 0.8rem;
}
.h5-op-item {
  width: 100%;
  padding: 0.6rem 1.5rem;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}
.h5-ques-body {
  margin-right: 10px;
}
.h5-qa-title {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}
.op-item > div.h5-op-select:first-child {
  left: 0.5rem;
  top: 0.8rem;
}
.h5-qac-bigInfo {
  font-size: 1rem;
  padding: 0.5rem 1.2rem;
  line-height: 1.3rem;
}
.h5-pcSpace {
  clear: both;
}

/*  */
.paper-answer-scrollBox .mCustomScrollBox {
  overflow: unset;
  overflow-y: hidden;
  overflow-x: unset;
}
.paper-answer-scrollBox .mCSB_inside > .mCSB_container {
  margin-right: 5px;
}
.paper-answer-scrollBox .mCSB_scrollTools {
  right: -15px;
}
</style> 





