let config = {
  // env: Env,
  apiDomain: 'http://frontapi.cedardev.cn/api/v1',
  // apiDomain: 'http://localhost:8088',
  // imgUpload: 'http://localhost:8082/upload'
  headers: {
    'Content-Type': 'application/json'
  },
  header() {
    return {
      'Authorization': 'Bearer ' + config.getCookie('zbxk-token')
    }
  },
  setCookie(cname, cvalue, exdays) {
    var d = new Date()
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
    var expires = "expires=" + d.toGMTString()
    document.cookie = cname + "=" + cvalue + "; " + expires
  },
  getCookie(cname) {
    var name = (cname ? cname : 'zbxk-token') + "=";
    var ca = document.cookie.split(';')
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i].trim()
      if (c.indexOf(name) == 0) return c.substring(name.length, c.length)
    }
    return "";
  },


  //
  moduleCodeTable: {
    'examlist': '',
    'psychological': '',
    'setting': '',
    'psychologicalreport':'',
  },
}
export default config


/**
 * , 
 * cookie
 *      1. zbxk-token   
 * localStorage:
 *      1. zbxk-current-exam 
 *      2. zbxk-cp-type
 *      3. zbxk-userInfo 
 *      4. zbxk-paperSaveParams   
 * 
 *  */



