/**
 *  (todo: promise)
 */
export const getReportHtml = (url, success, error) => {

  // // axios
  // return axios.get(url)

  var xhr = new XMLHttpRequest();
  xhr.onloadend = function () {
    if (xhr.readyState == 4 && xhr.status == 200) {
      success(xhr);
    }else{
      error(xhr);
    }
  };
  xhr.open('GET', url);
  xhr.send();

}



