<template>
  <div class="commonBox" v-loading.fullscreen.lock="boxloading" element-loading-text="..." element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
    <!--  -->
    <div class="height100" v-if="isNeedSignIn === false">
      <div class="psb-header">
        <div class="psbt-title ps-dashboard"></div>
        <div class="psbt-back" @click="backList" v-if="isPC"></div>
      </div>
      <div class="psb-content height100" v-scroll>
        <div>
          <div class="psdc-info">
            <p> </p>
            <p> [MBTI][Holland]</p>
          </div>
          <div class="psdc-examList">
            <div class="psdce-item">
              <div class="pi-img">
                <img class="fullSize" src='../../assets/mbti.png' />
              </div>
              <div class="pi-description">
                <div class="pid-title ">[MBTI]</div>
                <div class="pid-content"></div>
              </div>
              <div class="pi-btns">
                <div v-if='isMbiComplete === false' class="pib-test" @click="startExam('mbti')"></div>
                <!-- <div class="pib-test" @click="startExam('mbti')"></div> -->
                <div v-if='isMbiComplete === true' class="pib-report" @click="getReport('mbti')"></div>
              </div>
            </div>

            <div class="psdce-item">
              <div class="pi-img">
                <img class="fullSize" src='../../assets/holland.png' />
              </div>
              <div class="pi-description">
                <div class="pid-title">[Holland]</div>
                <div class="pid-content"></div>
              </div>
              <div class="pi-btns">
                <div v-if='isHollandComplete === false' class="pib-test" @click="startExam('holland')"></div>
                <!-- <div class="pib-test" @click="startExam('holland')"></div> -->

                <div v-if='isHollandComplete === true' class="pib-report" @click="getReport('holland')"></div>
              </div>
            </div>

            <div class="psdce-item">
              <div class="pi-img ">
                <img class="fullSize" src='../../assets/profession.png' />
              </div>
              <div class="pi-description">
                <div class="pid-title"></div>
                <div class="pid-content"></div>
              </div>
              <div class="pi-btns">
                <div v-if="isProfessionComplete === false" class="pib-test" @click="startExam('profession')"></div>
                <!-- <div class="pib-test" @click="startExam('profession')"></div> -->
                <div v-if="isProfessionComplete === true" class="pib-report pib-btn-disable"></div>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-if="isNeedSignIn === true" class="qr-code-login">
      <div class="loginBox" v-loading.fullscreen.lock="loading" element-loading-text="..." element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
        <div class="b-form">
          <div class='b-title'></div>
          <div>
            <div class='b-input'>
              <el-form :model="loginForm" ref="qrloginForm" label-width="100px" class="demo-ruleForm">
                <el-form-item prop="account" :rules="loginFormRules.account">
                  <el-input v-model="loginForm.account" placeholder="" @keyup.enter.native="qrSubmit"></el-input>
                </el-form-item>
                <el-form-item prop="password" :rules="loginFormRules.password">
                  <el-input type="password" v-model="loginForm.password" placeholder="" @keyup.enter.native="qrSubmit"></el-input>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class='b-login' @click="qrSubmit">&nbsp;</div>
          <div class='b-tip'>15963367879</div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import { checkPsyStatus, checkExamStatus, getStuInfo } from "@/api/http.js";
import common from "@/common.js";
import config from "@/config.js";
import axios from "axios";
export default {
  data() {
    //:
    //todo:token
    var currentExam = JSON.parse(
      localStorage.getItem("zbxk-current-exam") || "{}"
    );
    var checkNumber = function(rule, value, callback) {
      function isNum() {
        return new RegExp("\\D", "g");
      }
      if (isNum().test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      common,
      axios,
      config,
      currentExam: currentExam,
      checkExamStatusAPI: checkExamStatus,
      checkPsyStatusAPI: checkPsyStatus,
      getStuInfoAPI: getStuInfo,

      //pc
      isPC: false,

      //
      routerParams: {},

      //  
      isHollandComplete: "",
      isMbiComplete: "",
      isProfessionComplete: "",

      //
      isNeedSignIn: "",
      loading: false,

      //
      account: "",
      password: "",
      loginForm: {
        account: "",
        password: ""
      },
      boxloading: true,
      loginFormRules: {
        account: [
          { required: true, message: "" },
          { validator: checkNumber, trigger: "change" }
        ],
        password: [{ required: true, message: "" }]
      },
      isRemember: true
    };
  },
  mounted() {
    var that = this;
    // if (!common.checkPc()) {
    //   that.SetPhonecss();
    // }
    that.checkIsNeedSignIn();
    this.$nextTick(function() {
      //nextTick layout
      that.bus.$emit("toChangeTitle", "");
    });
  },

  beforeRouteEnter(to, from, next) {
    /**
     *    [  h5]
     * 1. 
     * 2.  demo   http://localhost:8080/#/main/psychological?test_id=9964458&school_id=468468   
     * 3.        demo    http://localhost:8080/#/main/psychological?test_id=9964458&school_id=468468  
     * 4.      demo    http://localhost:8080/#/main/psychological?test_id=9964458&school_id=s468468&sno=65468&sname=65465465   
     */
    next();
  },
  methods: {
    startExam(type) {
      // 
      var params = { name: "psychologicalpaper", params: { type: type } };
      var router_keys = Object.keys(this.routerParams);
      var routesParamsObj = this.routerParams;
      if (router_keys.length) {
        params.query = {};
        router_keys.forEach(function(key) {
          params.query[key] = routesParamsObj[key];
        });
      }

      localStorage.setItem("zbxk-cp-type", type);
      this.$router.push(params);
    },
    getReport(type) {
      localStorage.setItem("zbxk-cp-type", type);
      this.$router.push({ name: "psychologicalreport" });
    },
    backList() {
      this.$router.push({ name: "examlist" });
    },
    //
    transformPhone() {
      var that = this;
      setTimeout(function() {
        if (!that.common.checkPc()) {
          that.SetPhonecss();
          return;
        }
        that.isPC = true;
      });
    },
    SetPhonecss: function(value) {
      var Btnlist = document.getElementsByClassName("psdce-item");
      for (var i = 0; i < Btnlist.length; i++) {
        Btnlist[i].classList.add("tiemFlex");
      }
    },

    /**
     * // 
     * // ------
     * 18556823593
     * 666666ab
     */
    //     234
    checkIsNeedSignIn() {
      /**
       *
       */
      this.boxloading = true;
      // var hasToken = this.config.getCookie("zbxk-token");
      // console.log(hasToken);
      // hasToken = 0;
      var fullPath = this.$route.fullPath,
        qpString = "",
        queryArray = [],
        qpObject = {};
      // fullPath.replace(/(?<=(\/main\/psychological\s*\?)).*/g, function(match) {
      //   qpString = match;
      //   return match;
      // });
      fullPath.replace(/\/main\/psychological\s*\?(.*)/g, function(match, $1) {
        qpString = $1;
        return match;
      });
      queryArray = qpString ? qpString.split("&") : [];
      if (!queryArray.length) {
        var userInfo = JSON.parse(localStorage.getItem("zbxk-userInfo")),
          param = { school_id: userInfo.school_id };
        this.getSno(param, "login-true");
      } else {
        queryArray.forEach(function(item) {
          var params = item.split("=");
          ["test_id", "school_id", "sno", "sname"].forEach(function(key) {
            if (item.indexOf(key) !== -1) {
              qpObject[key] = params[1];
            }
          });
        });
        this.routerParams = qpObject;
        this.checkNeedSignAPI(qpObject);
      }
    },
    checkNeedSignAPI(qpObject) {
      //qpObject:
      //1.  "test_id", "school_id", "sno", "sname"
      //2.  "test_id", "school_id",
      var that = this;
      var keylength = Object.keys(qpObject).length;
      if (keylength === 4) {
        that.setPaperSaveParams(qpObject);
        //
        var _checkBtnParam = {
          test_id: qpObject.test_id,
          school_id: qpObject.school_id,
          sno: qpObject.sno
        };
        this.checkBtnStatus(_checkBtnParam);
      } else if (keylength === 2) {
        if (!qpObject["test_id"] || !qpObject["school_id"]) {
          that.$message.error("");
          return;
        }
        var _param = {
          test_id: qpObject["test_id"],
          school_id: qpObject["school_id"]
        };
        //
        that.checkExamStatusAPI(_param).then(function(res) {
          var result = res.data;
          if (result.code === 200) {
            var type = result.data.test_type;
            console.log(type);
            //4 
            if (type == 4) {
              var _paperSaveParam = {
                test_id: that.currentExam.test_id,
                sno: "todo",
                username: "todo",
                school_id: that.currentExam.school_id,
                class: that.currentExam.class_num
              };
              that.setPaperSaveParams(_paperSaveParam);
              //
              that.isHollandComplete = false;
              that.isMbiComplete = false;
              that.isProfessionComplete = false;
              that.resetLoading();
            } else {
              // 
              var hasToken = that.config.getCookie("zbxk-token");
              if (hasToken) {
                var param = { school_id: result.data.school_id };
                that.boxloading = true;
                that.getSno(param, "login-true");
                return;
              }
              //
              that.isNeedSignIn = true;
              that.boxloading = false;
            }
          } else {
            that.$message.error(result.message);
          }
          that.transformPhone();
        }, this.error);
      }
    },
    resetLoading: function() {
      this.isNeedSignIn = false;
      this.boxloading = false;
      this.transformPhone();
    },
    // 
    setPaperSaveParams: function(data) {
      //  sname username
      var _data = JSON.parse(JSON.stringify(data));
      if (_data.sname) {
        _data.username = _data.sname;
      }
      localStorage.setItem("zbxk-paperSaveParams", JSON.stringify(_data));
    },
    //sno
    /**
     * param
     *    {
     *      school_id: 24324
     *    }
     */
    getSno(param, type) {
      var that = this;
      this.getStuInfoAPI(param).then(function(res) {
        var result = res.data;
        if (result.code === 200) {
          that.snoSuccessBack(result.data, type);
        } else {
          that.resetLoading();
          that.$message.error(result.message);
        }
      }, this.error);
    },
    //
    error(res) {
      this.resetLoading();
      that.$message.error("");
    },
    snoSuccessBack(result, type) {
      /**
       * type
       *    "login-true":   
       *    "login-false":  
       */
      switch (type) {
        case "login-true":
          var userInfo = JSON.parse(localStorage.getItem("zbxk-userInfo"));
          //psychologicalpaper
          var params = {
            test_id: this.currentExam.test_id,
            sno: result.student_no,
            username: userInfo.name,
            school_id: this.currentExam.school_id,
            class: this.currentExam.class_num
          };
          this.setPaperSaveParams(params);
          //
          var _checkBtnParam = {
            test_id: params.test_id,
            school_id: params.school_id,
            sno: params.sno
          };
          this.checkBtnStatus(_checkBtnParam);
          break;
        case "login-false":
          break;
        default:
          break;
      }
    },
    /**
     * 
     * @param:
     *  test_id	 	 ID
     *  school_id	 ID
     *  sno        )
     */
    checkBtnStatus(param) {
      var that = this;
      this.checkPsyStatusAPI(param).then(function(res) {
        var result = res.data;
        if (result.code === 200) {
          var _status = result.data;
          that.isHollandComplete = _status.holland;
          that.isMbiComplete = _status.mbti;
          that.isProfessionComplete = _status.major;
        } else {
          that.$message.error(result.message);
        }
        that.resetLoading();
      }, this.error);
    },

    //
    qrSubmit() {
      var that = this;
      this.$refs["qrloginForm"].validate(function(valid) {
        if (valid) {
          that.qrloginAPI();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    getUserInfo() {
      var that = this,
        config = this.config;
      var url = config.apiDomain + "/user/info";
      this.axios.get(url, { headers: config.header() }).then(
        res => {
          // 
          var data = res.data;
          if (data.code == 200) {
            localStorage.setItem("zbxk-userInfo", JSON.stringify(data.data)); //token
            that.isNeedSignIn = false;
          }
          this.loading = false;
          var param = { school_id: this.routerParams.school_id };
          this.boxloading = true;
          this.getSno(param, "login-true");
        },
        res => {
          // 
          console.log(res);
          this.loading = false;
        }
      );
    },
    //
    qrloginAPI() {
      var that = this,
        config = this.config;
      // 
      var url = config.apiDomain + "/student/account-login";
      //todo: 5IP2
      var codeUrl = config.apiDomain + "/user/captcha";
      this.loading = true;
      this.axios.post(url, this.loginForm, { headers: config.headers }).then(
        res => {
          // 
          var data = res.data;
          if (data.code == 200) {
            config.setCookie("zbxk-token", data.data.token, 1); //token
            that.getUserInfo();
          } else {
            that.$message({
              message: data.message,
              type: "warning"
            });
            this.loading = false;
          }
        },
        res => {
          // 
          console.log(res);
          this.loading = false;
        }
      );
    }
  }
};
</script>
<style>
.psbt-title.ps-dashboard::before {
  background: url(../../assets/psychological.png) no-repeat;
}

/*  */
.psb-content {
  text-align: left;
  padding: 16px;
}
.psdc-info {
  font-size: 14px;
  color: #284050;
  letter-spacing: 0;
  line-height: 18px;
  /* width: 70%;*/
  margin-bottom: 24px;
}

.pid-title {
  font-size: 20px;
  color: #284050;
  letter-spacing: 0;
  line-height: 20px;
  margin-bottom: 16px;
}
.pid-content {
  font-size: 14px;
  color: #6d8392;
  letter-spacing: 0;
  margin-bottom: 40px;
}
/*.psdce-item {
  margin-bottom: 25px;
  margin-right: 20px;
}*/

.pi-btns > div {
  display: inline-block;
  border: 1px solid #4393f7;
  border-radius: 4px;
  font-size: 14px;
  text-align: center;
  padding: 6px 40px;
  background: #fff;
  color: #4393f7;
}
.pi-btns > div:hover {
  background: #4393f7;
  color: #ffffff;
  cursor: pointer;
}
.pi-btns > div.pib-btn-disable {
  background: #d7dce0;
  border: 1px solid #d7dce0;
  border-radius: 4px;
  color: #fff;
  cursor: not-allowed;
}

.pi-img {
  /* -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-position: center 0;
  border-radius: 4px; */
  /* width: 448px;*/
  /* height: 280px;  */
  margin-bottom: 20px;
}
.pi-img.mbti {
  background: url(../../assets/mbti.png) no-repeat;
}
.pi-img.holland {
  background: url(../../assets/holland.png) no-repeat;
}
.pi-img.profession {
  background: url(../../assets/profession.png) no-repeat;
}

/*  */
.psdc-examList {
  display: flex;
  /*  flex-direction: row;*/
  flex-wrap: wrap;
}
.psdce-item {
  margin-bottom: 50px;
  margin-right: 20px;
  flex: 1;
}

.fullSize {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.tiemFlex {
  flex: auto;
  margin-right: 0;
}
.tiemFlex .pi-btns > div {
  display: block;
  margin-bottom: 5px;
}

/*  */
.qr-code-login {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #284050;
  z-index: 100;
}

.loginBox {
  height: 100%;
  background: url(../../assets/login.png) no-repeat;
  -webkit-background-size: cover;
  -o-background-size: cover;
  /* background-position: center 0; */
  display: flex;
  align-items: center; /**/
  justify-content: center; /**/
}

.b-form {
  width: 480px;
  height: 380px;
  background: #fff;
  border-radius: 8px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 35px 50px;
}
.b-title {
  font-size: 30px;
  margin-bottom: 40px;
}
.b-input .el-input input {
  color: #284050;
}
.b-input > div {
  margin-bottom: 20px;
}
.b-input .el-form-item__content {
  margin-left: 0 !important;
}
.b-check {
  float: left;
  margin-bottom: 20px;
}
.b-login {
  clear: both;
  background: #419bf9;
  border-radius: 5px;
  color: #fff;
  padding: 10px 0;
}
.b-login:hover {
  cursor: pointer;
  opacity: 0.8;
}

.b-tip {
  margin-top: 15px;
  font-size: 14px;
  color: #989a9c;
}

.height100 {
  height: 100%;
}

/* element */
.el-checkbox__input.is-checked + .el-checkbox__label {
  color: inherit !important;
}
/* .el-loading-mask {
  background-color: rgba(255, 255, 255, 0.2) !important;
} */
</style>





